<template>
  <div>
    <div class="read_center_box" id="read_center_box">
      <!-- <p class="dqyd">
        <img src="@/assets/icon/read_ico_sj.png" /> 您已阅读{{
          readingTimeCount | stohms
        }}
      </p> -->
      <div id="divider" class="show" v-show="flow === 'auto'"></div>
      <div id="box" v-show="boxViewer">
        <div id="viewer" class="scrolled"></div>
      </div>
    </div>
    <div class="read_right_box">
      <div class="read_right_box1">
        <el-popover
          placement="left"
          width="500"
          trigger="click"
          v-model="muluvisible"
        >
          <i
            class="el-icon-error"
            style="
              float: right;
              margin-top: -5px;
              margin-bottom: 5px;
              cursor: pointer;
            "
            @click="muluvisible = false"
          ></i>
          <div class="mulu rmulu">
            <li v-for="(item, index) in mulu" :key="index">
              <p class="ellipsis-aRow" @click="clickmulu(item)">
                {{ item.label }}
              </p>
              <ul v-if="item.subitems.length" class="twomulu">
                <li
                  v-for="(item2, index2) in item.subitems"
                  :key="index2"
                  @click="clickmulu(item2)"
                >
                  {{ item2.label }}
                </li>
              </ul>
            </li>
          </div>
          <li slot="reference">
            <img
              v-if="backclass !== 'zt_heise'"
              src="@/assets/icon/read_ico_mulu.png"
            />
            <img
              v-if="backclass === 'zt_heise'"
              src="@/assets/icon/read_ico_mulu_bai.png"
            />
            <p>目录</p>
          </li>
        </el-popover>
        <el-popover
          placement="left"
          width="350"
          trigger="click"
          v-model="shezhivisible"
        >
          <i
            class="el-icon-error"
            style="
              float: right;
              margin-top: -5px;
              margin-bottom: 5px;
              cursor: pointer;
            "
            @click="shezhivisible = false"
          ></i>
          <div class="shezhi">
            <li>
              <p>阅读主题</p>
              <div class="xz_box ydzt">
                <span
                  v-for="(item, index) in backgroundList"
                  :key="index"
                  :class="item.backclass === backclass ? 'active' : ''"
                  :style="'background: ' + item.color"
                  @click="changezt(item), result(), shezhisave()"
                ></span>
              </div>
            </li>
            <li>
              <p>正文字体</p>
              <div class="xz_box zwzt">
                <span
                  @click="changezwzt(item.value), shezhisave()"
                  :class="zwzt === item.value ? 'active' : ''"
                  v-for="(item, index) in zwztList"
                  :key="index"
                  >{{ item.label }}</span
                >
              </div>
            </li>
            <li>
              <p>字体大小</p>
              <div class="xz_box ztdx">
                <el-input-number
                  v-model="num"
                  @change="numchange()"
                  size="small"
                  :min="12"
                  :max="36"
                  label="描述文字"
                  :step="2"
                  step-strictly
                  @blur="
                    (e, name) => {
                      blurNumber(e, 'num');
                    }
                  "
                ></el-input-number>
              </div>
            </li>
          </div>
          <li slot="reference">
            <img
              v-if="backclass !== 'zt_heise'"
              src="@/assets/icon/read_ico_shezhi.png"
            />
            <img
              v-if="backclass === 'zt_heise'"
              src="@/assets/icon/read_ico_shezhi_bai.png"
            />
            <p>设置</p>
          </li>
        </el-popover>
        <!-- <el-popover
          placement="left"
          width="500"
          trigger="click"
          v-model="bijivisible"
        >
          <i
            class="el-icon-error"
            style="
              float: right;
              margin-top: -5px;
              margin-bottom: 5px;
              cursor: pointer;
            "
            @click="bijivisible = false"
          ></i>
          <div class="mulu">
            <li v-for="(item, index) in highlights" :key="index">
              <p
                style="cursor: pointer"
                @click="highlightClick(item.textRange)"
              >
                {{ item.noteContent }}
              </p>
              <div class="sjcz">
                <p style="float: left">{{ item.createTime }}</p>
                <div class="mulu_liright">
                  <span @click="updateNote(index)">
                    <i class="el-icon-edit"></i>
                    修改
                  </span>
                  <span @click="removeNote(item.textRange, index)">
                    <i class="el-icon-delete"></i>
                    删除
                  </span>
                </div>
              </div>
            </li>
            <noData v-if="highlights == 0"></noData>
          </div>
          <li slot="reference">
            <img
              v-if="backclass !== 'zt_heise'"
              src="@/assets/icon/read_ico_biji.png"
            />
            <img
              v-if="backclass === 'zt_heise'"
              src="@/assets/icon/read_ico_biji_bai.png"
            />
            <p>笔记</p>
          </li>
        </el-popover> -->
        <!-- <li
          :class="
            this.$route.query.typeName == 0 && isBookshelf === true
              ? 'cursorDefault'
              : ''
          "
          @click="addBookshelf()"
        >
          <img
            v-if="backclass !== 'zt_heise'"
            src="@/assets/icon/read_ico_shujia.png"
          />
          <img
            v-if="backclass === 'zt_heise'"
            src="@/assets/icon/read_ico_shujia_bai.png"
          />
          <p v-if="isBookshelf">已加书架</p>
          <p v-if="!isBookshelf">加入书架</p>
        </li> -->
        <li @click="changeflow">
          <div v-if="backclass !== 'zt_heise'">
            <img
              v-if="flow === 'auto'"
              src="@/assets/icon/read_ico_huadong.png"
            />
            <img
              v-if="flow !== 'auto'"
              src="@/assets/icon/read_ico_fanye.png"
            />
          </div>
          <div v-if="backclass === 'zt_heise'">
            <img
              v-if="flow === 'auto'"
              src="@/assets/icon/read_ico_huadong_bai.png"
            />
            <img
              v-if="flow !== 'auto'"
              src="@/assets/icon/read_ico_fanye_bai.png"
            />
          </div>
          <p>{{ flow === "auto" ? "滑动阅读" : "翻页阅读" }}</p>
        </li>
      </div>
      <div class="read_right_box2">
        <div id="prev">
          <img @click="prevPage()" src="@/assets/icon/read_ico_shang.png" />
        </div>
        <div id="next">
          <img @click="nextPage()" src="@/assets/icon/read_ico_xia.png" />
        </div>
      </div>
    </div>
    <div class="slider_box">
      <el-slider
        id="slider"
        v-model="progress"
        @change="onProgressChange"
        :max="sliderMax"
        :format-tooltip="sliderFormat"
        :show-tooltip="flag"
      >
      </el-slider>
    </div>
  </div>
</template>

<script>
import "jszip";
import Epub from "epubjs";
import { ImagePreview } from "vant";
import {
  insertNote,
  update,
  selectNoteListByFileId,
  removeNoteById,
  selectFileLinkAndReadingLocation, //查看阅读文件地址和文件进度
  selectReadInfo, //获取阅读时长
  selectTimeParameter, //获取调用阅读时长接口频率
  updateOcDownloadReading, //更新用户阅读时长
  updateOcReadSchedule, //更新阅读进度
} from "@/api/special/special";
import {
  IsBookshelfAdd,
  BookshelfAdd,
  delBookshelf,
  SelGoodsStatus,
} from "@/api/bookstore/bookstore";
import { checkIsBuy, isHavingPermissions } from "@/api/bookstore/bookstore";
import noData from "@/components/noData.vue";
export default {
  components: {
    noData,
  },
  data() {
    return {
      loading: false,
      goodsId: this.$route.query.goodsId, //需要传递的参数
      filesId: "", //需要传递的参数(资源文件id)
      goodsFilePath: "", //需要传递的参数(资源文件路径)
      book: {},
      rendition: [],
      highlights: [],
      note: {},
      displayed: {},
      bijivisible: false,
      muluvisible: false,
      shezhivisible: false,
      flag: true,
      isRTL: true,
      chapterName: "",
      readingLocation: "",
      readTime: "",
      progress: 0,
      flow: "auto", //scrolled=>竖版   auto=>横版
      mulu: undefined, //目录
      num: 16, //字体
      locations: {},
      backclass: "zt_moren", //主题的背景颜色class
      backgroundList: [
        {
          backclass: "zt_moren",
          color: "#f8f4e9",
        },
        {
          backclass: "zt_niupizhi",
          color: "#dbcfa5",
        },
        {
          backclass: "zt_danlvse",
          color: "#ebf1e5",
        },
        {
          backclass: "zt_danlanse",
          color: "#e9f5f5",
        },
        {
          backclass: "zt_danfense",
          color: "#f4e9e9",
        },
        {
          backclass: "zt_huise",
          color: "#e6e2e2",
        },
        {
          backclass: "zt_heise",
          color: "#424242",
        },
      ],
      sliderMax: 100, //进度条默认最大值
      sliderList: [], //slider数据
      zwzt: "'Microsoft YaHei',PingFangSC-Regular,HelveticaNeue-Light,'Helvetica Neue Light',sans-serif", //字体
      zwztList: [
        {
          value:
            "'Microsoft YaHei',PingFangSC-Regular,HelveticaNeue-Light,'Helvetica Neue Light',sans-serif",
          label: "雅黑",
        },
        {
          value: "STKaiti,Kaiti",
          label: "楷书",
        },
      ], //字体列表
      bookdetail: {}, //基本信息
      sourceList: [], //文件信息
      readdefault: 0, //默认阅读第一个
      // buystatus:'1',//购买状态0=》已购买随便读  1=》未购买只能读免费
      TimeParameter: 10, //更新频次-单位分钟
      readingTimeCount: 0, //阅读时间-单位秒
      setInterval2: undefined,
      setInterval1: undefined,
      isBookshelf: false,
      cfiRange: "", //划中的选区地址 ，用selected事件来获取暂存到此参数，mouseup事件时使用
      Trial: false,
      boxViewer: true,
    };
  },
  watch: {
    //监听路由变化
    $route() {
      if (this.$route.query.goodsId) {
      }
    },
  },
  created() {
    if (this.$route.query.isTrial == "true") {
      this.Trial = true;
    } else {
      this.Trial = false;
    }
  },
  // destroyed() {
  //   this.closeTime();
  // },
  methods: {
    tabbook(item, index, filePath, readingLocation) {
      // this.Trial = false; //试读
      // this.IsBookshelfAdd();
      if (localStorage.getItem("shezhisave")) {
        var newshezhi = JSON.parse(localStorage.getItem("shezhisave"));
        this.num = newshezhi.num;
        this.zwzt = newshezhi.zwzt;
        this.backclass = newshezhi.backclass;
        this.$store.state.read.backclass = newshezhi.backclass;
      }
      this.goodsFilePath = filePath;
      this.showEpub(readingLocation); //加载epub，传之前的阅读位置
    },

    blurNumber(e, name) {
      if (!e.target.value) {
        this.num = 12;
      }
    },
    //进度条显示百分比
    sliderFormat(value) {
      if (value != 0) {
        value = parseFloat(value).toFixed(0) + "%";
      }
      return value;
    },
    // 电子书的解析和渲染
    showEpub(readingLocation) {
      const loading = this.$loading({
        lock: true,
        text: "Loading",
      });
      var _this = this;
      // 将epub文件路径 转为解压后的文件
      let epubFilePath =
        this.goodsFilePath.split(".epub")[0] + "/container.xml";
      // 生成book
      this.book = new Epub(epubFilePath);
      this.rendition = this.book.renderTo("viewer", {
        width: window.innerWidth - 450,
        height: window.innerHeight - 112,
        // manager: "continuous",
        flow: this.flow,
      });
      if (readingLocation) {
        this.rendition.display(readingLocation);
      } else {
        this.rendition.display();
      }
      // 使用左右键切换上下页
      this.book.ready.then(() => {
        let next = document.getElementById("next");
        let keyListener = function (e) {
          // 左键
          if ((e.keyCode || e.which) == 37) {
            _this.book.package.metadata.direction === "rtl"
              ? _this.nextPage()
              : _this.prevPage();
          }
          // 上键
          if ((e.keyCode || e.which) == 38) {
            _this.book.package.metadata.direction === "rtl"
              ? _this.nextPage()
              : _this.prevPage();
          }

          // 右键
          if ((e.keyCode || e.which) == 39) {
            _this.book.package.metadata.direction === "rtl"
              ? _this.prevPage()
              : _this.nextPage();
          }
          // 下键
          if ((e.keyCode || e.which) == 40) {
            _this.book.package.metadata.direction === "rtl"
              ? _this.prevPage()
              : _this.nextPage();
          }
        };
        _this.rendition.on("keyup", keyListener);
        document.addEventListener("keyup", keyListener, false);
      });
      this.rendition.on("relocated", function (location) {
        var next =
          _this.book.package.metadata.direction === "rtl"
            ? document.getElementById("prev")
            : document.getElementById("next");
        var prev =
          _this.book.package.metadata.direction === "rtl"
            ? document.getElementById("next")
            : document.getElementById("prev");

        if (location.atEnd) {
          next.style.visibility = "hidden";
        } else {
          next.style.visibility = "visible";
        }

        if (location.atStart) {
          prev.style.visibility = "hidden";
        } else {
          prev.style.visibility = "visible";
        }
      });
      this.book.ready
        .then(() => {
          this.navigation = _this.book.navigation;
          //生成locations对象的定位符
          return _this.book.locations.generate();
        })
        .then(() => {
          //保存loactions对象
          this.locations = _this.book.locations;
          //电子书渲染完
          this.bookAvailable = true;
          const currentLocation = _this.rendition.currentLocation();
          const progress =
            Math.floor(
              _this.locations
                .percentageFromCfi(currentLocation.start.cfi)
                .toFixed(5) * 10000
            ) / 100;
          _this.progress = progress;
        });
      this.book.loaded.navigation.then(function (toc) {
        let tocLlength = toc.length;
        _this.sliderList = _this.treeToArray(
          JSON.parse(JSON.stringify(toc.toc))
        );
        _this.mulu = toc.toc;
        setTimeout(() => {
          _this.result();
          loading.close();
        }, 1000);
      });
      this.rendition.on("selected", (cfiRange) => {
        this.cfiRange = cfiRange;
      });
      this.rendition.on("click", (e) => {
        if (e.srcElement.tagName === "IMG") {
          ImagePreview({ images: [e.srcElement.src], closeable: true });
        }
      });
      this.rendition.on("mouseup", (e, contents) => {
        if (this.cfiRange === "") {
          return;
        }
        // this.rendition.annotations.highlight(this.cfiRange, {}, e => {});
        contents.window.getSelection().removeAllRanges();
        this.book.getRange(this.cfiRange).then((range) => {
          _this.isRTL = false;
          // this.rendition.annotations.remove(this.cfiRange, "highlight");
          // if (range) {
          //   this.note = {
          //     goodsId: this.goodsId,
          //     goodsFileId: this.filesId,
          //     originalText: range.toString(),
          //     textRange: this.cfiRange,
          //     href: "#" + this.cfiRange,
          //     noteContent: "",
          //   };
          //   this.$prompt("请输入笔记内容", "提示", {
          //     inputType: "textarea",
          //     confirmButtonText: "确定",
          //     cancelButtonText: "取消",

          //     closeOnClickModal: false,
          //     inputValidator: (value) => {
          //       // 点击按钮时，对文本框里面的值进行验证
          //       if (!value || /^\s+$/.test(value)) {
          //         return "请输入笔记内容";
          //       }
          //       if (value.length > 100) {
          //         return "不能超过100字";
          //       }
          //     },
          //   })
          //     .then(({ value }) => {
          //       this.note.noteContent = value;
          //       let _this = this;
          //       //添加笔记内容
          //       insertNote(_this.note).then((res) => {
          //         _this.$message({
          //           type: "success",
          //           message: "操作成功",
          //         });
          //         // _this.getNoteList();
          //         _this.bijivisible = true;
          //         _this.isRTL = true;
          //         this.cfiRange = "";
          //       });
          //     })
          //     .catch(() => {
          //       this.rendition.annotations.remove(this.cfiRange, "highlight");
          //       this.cfiRange = "";
          //       this.isRTL = true;
          //     });
          // }
        });
      });
    },
    //tree=>list
    treeToArray(list, newArr = []) {
      list.forEach((item) => {
        const { subitems } = item;
        if (subitems) {
          delete item.subitems;
          if (subitems.length) {
            newArr.push(item);
            return this.treeToArray(subitems, newArr);
          }
        }
        newArr.push(item);
      });
      return newArr;
    },
    onProgressChange(progress) {
      console.log(progress);
      let pro = progress;
      if (progress > 10 && this.Trial) {
        this.$message("该商品试读结束");
        pro = 10;
      } else {
        pro = progress;
      }
      this.progress = pro;
      const percentage = pro / 100;
      //通过this.locations.cfiFromPercentage(percentage)获取到当前的页数
      const location =
        percentage > 0 ? this.locations.cfiFromPercentage(percentage) : "";
      //传入location进行定位渲染
      if (location) {
        this.rendition.display(location);
      } else {
        this.rendition.display();
      }
    },
    result() {
      this.changezt({
        backclass: this.backclass,
      });
      this.numchange();
      this.changezwzt(this.zwzt);
      var hei = this.backclass === "zt_heise" ? "#ccc" : "#000";
      this.rendition.themes.default({
        // "::selection": {
        //   background: "rgba(255,255,0, 0.3)"
        // },
        ".epubjs-hl": {
          fill: "yellow",
          "fill-opacity": "0.3",
          "mix-blend-mode": "multiply",
        },
        body: {
          background: "rgba(255,255,255, 0)",
        },
        body: {
          color: hei,
        },
        body: {
          "background-size": "100%",
        },
        body: {
          "background-image": "none",
          "background-size": "100%",
          "background-repeat": "no-repeat",
        },
        video: {
          "max-width": "90%",
        },
      });
    },
    clickmulu(item) {
      let _this = this;
      this.rendition.display(item.href).then(() => {
        const currentLocation = _this.rendition.currentLocation();
        const progress =
          Math.floor(
            _this.locations
              .percentageFromCfi(currentLocation.start.cfi)
              .toFixed(5) * 10000
          ) / 100;
        _this.progress = progress;
        console.log(progress);
        if (_this.progress > 10 && this.Trial) {
          _this.progress = 10;
          const location = _this.locations.cfiFromPercentage(
            _this.progress / 100
          );
          if (location) {
            this.rendition.display(location);
          }
        }
      });
      _this.result();
      _this.muluvisible = false;
    },
    highlightClick(epubcfi) {
      let _this = this;
      setTimeout(() => {
        //点击目录获取进度，进度条滑动到指定位置
        this.rendition.display(epubcfi).then(() => {
          const currentLocation = _this.rendition.currentLocation();
          const progress =
            Math.floor(
              _this.locations
                .percentageFromCfi(currentLocation.start.cfi)
                .toFixed(5) * 10000
            ) / 100;
          _this.progress = progress;
        });
        this.result();
        this.bijivisible = false;
      }, 500);
    },
    changezt(item) {
      //更换主题
      this.backclass = item.backclass;
      this.$store.state.read.backclass = item.backclass;
    },
    numchange() {
      if (this.num <= 12) {
        this.num = 12;
      } else if (this.num >= 48) {
        this.num = 48;
      }
      this.shezhisave();
      this.rendition.themes.fontSize(this.num + "px");
      // this.initNoteList();
    },
    changezwzt(fontfamily) {
      this.zwzt = fontfamily;
      this.rendition.themes.font(this.zwzt);
    },
    updateNote(index) {
      this.isRTL = false;
      this.note = this.highlights[index];
      this.$prompt("请输入笔记内容", "提示", {
        inputValue: this.note.noteContent,
        inputType: "textarea",
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        closeOnClickModal: false,
        inputValidator: (value) => {
          // 点击按钮时，对文本框里面的值进行验证
          if (!value || /^\s+$/.test(value)) {
            return "请输入笔记内容";
          }
          if (value.length > 100) {
            return "不能超过100字";
          }
        },
      })
        .then(({ value }) => {
          this.note.noteContent = value;
          //修改笔记内容
          update(this.note).then((response) => {
            this.$message({
              type: "success",
              message: "操作成功",
            });
            // this.getNoteList();
            this.bijivisible = true;
            this.isRTL = true;
          });
        })
        .catch(() => {
          this.isRTL = true;
        });
    },
    removeNote(epubcfi, index) {
      this.$confirm("确认删除吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.rendition.annotations.remove(epubcfi, "highlight");
        removeNoteById(this.highlights[index].id).then((response) => {
          // this.getNoteList();
          this.$message({
            type: "success",
            message: "删除成功",
          });
          this.bijivisible = true;
        });
        return false;
      });
    },
    shezhisave() {
      setTimeout(() => {
        var shezhisave = {
          num: this.num, //字体
          backclass: this.backclass, //主题
          zwzt: this.zwzt, //字体
        };
        localStorage.setItem("shezhisave", JSON.stringify(shezhisave));
      }, 300);
    },
    // 电子书切换到上一页
    prevPage() {
      /* let prev = this.rendition.prev();*/
      let _this = this;
      if (_this.isRTL) {
        this.rendition.prev().then(() => {
          const currentLocation = _this.rendition.currentLocation();
          const progress =
            Math.floor(
              _this.locations
                .percentageFromCfi(currentLocation.start.cfi)
                .toFixed(5) * 10000
            ) / 100;
          _this.progress = progress;
        });
      }
    },
    // 电子书切换到下一页
    nextPage() {
      let _this = this;
      const currentLocation = _this.rendition.currentLocation();
      let progress = "";
      if (currentLocation) {
        progress =
          Math.floor(
            _this.locations
              .percentageFromCfi(currentLocation.start.cfi)
              .toFixed(5) * 10000
          ) / 100;
        if (progress > 10 && this.Trial) {
          this.$message("该商品试读结束");
          return;
        }
      }
      if (_this.isRTL) {
        this.rendition.next().then(() => {
          _this.progress = progress;
        });
      }
    },
    //加入书架
    addBookshelf() {},
    changeflow() {
      if (this.flow === "auto") {
        this.flow = "scrolled";
      } else {
        this.flow = "auto";
      }
      setTimeout(() => {
        this.book.rendition.flow(this.flow);
        this.result();
        this.rendition.display(this.rendition.location.start.cfi);
      }, 100);
    },
    initNoteList() {
      //初始化笔记
      selectNoteListByFileId(this.filesId).then((response) => {
        this.highlights = response.data;
        for (let item of response.data) {
          //将查出来的每一条笔记高亮显示
          this.rendition.annotations.remove(item.textRange, "highlight");
          this.rendition.annotations.highlight(item.textRange, {}, (e) => {});
        }
        this.loading = false;
      });
    },
    getNoteList() {
      //查询笔记
      selectNoteListByFileId(this.filesId).then((response) => {
        this.highlights = response.data;
        this.loading = false;
      });
    },

    updateOcReadSchedule() {
      //更新阅读进度
      const percentage = this.progress / 100;
      const location =
        percentage > 0 ? this.locations.cfiFromPercentage(percentage) : 0;
      if (location && location != 0) {
        this.$emit("readSchedule", location, percentage);
      }
    },
  },
};
</script>
